<template>
  <div>
    <p>:</p>
    <el-form ref="form" :model="form" label-width="100px" size="medium">
      <el-form-item label="">
        <el-input v-model="form.Brand"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.Number"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.Frame"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.Engine"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="form.Vehicle" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="onCancel"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Demo from "@/js/create";

export default {
  data() {
    return {
      activeIndex2: "1",
      form: {
        Brand: "",
        Number: "",
        Frame: "",
        Engine: "",
        Vehicle: "",
        date1: "",
        date2: ""
      }
    };
  },
  created: async function() {
    await Demo.init_user();
  },
  methods: {
    async onCancel() {
      this.$router.push({ name: "page1", params: { id: "2" } });
    },
    async onSubmit() {
      try {
          await Demo.addvaluation(
          this.form.Frame.toString(),
          this.form.Number.toString(),
          this.form.Vehicle.toString(),
          this.form.Brand.toString(),
          this.form.Engine.toString(),
          this.form.date1.toLocaleDateString(),
          new Date().toLocaleString()
        );

        this.$router.push({ name: "create2", params: { id: "3" } });
      }
      catch(err) {
        alert(",");
        console.log(err);
      }
      
    }
  }
};
</script>




