<template>
  <div>
    <el-table
      ref="singleTable"
      :data="tableData"
      highlight-current-row
      style="width: 100%"
    >
      <el-table-column fixed label="" type="index" width="50"></el-table-column>

      <el-table-column prop="address" label="" width="250"></el-table-column>
      <el-table-column prop="name" label="" width="300"></el-table-column>
      <el-table-column prop="vin" label="" width="180"></el-table-column>
      <el-table-column prop="money" label="" width="100"></el-table-column>
      <el-table-column prop="state" label="" width="90"></el-table-column>
      <el-table-column prop="date" label="" width="170"></el-table-column>

      <el-table-column fixed="right" label="" width="140">
        <template slot-scope="scope">
          <el-button @click="valuation(scope.row)" type="text" size="small"></el-button>
          <el-button @click="back(scope.row)" type="text" size="small"></el-button>
          <el-button @click="detail(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import Create from "@/js/create";
import Eva from "@/js/evaluate";

var tmp;

export default {
  created: async function() {
    await Create.init_user();
    await Eva.init_user();
    
    var temp = await Eva.backvalnumber();
    var num = temp.length;
    var result = "[";

    var str;
    var status;
    var stick = new Array();

    for (var i = 0; i < num; i++) {
      tmp = await Create.test1(temp[i], 9);
      var money = await Create.test1(temp[i], 7);
      if(tmp == "0") {
        status = "";
        money = "";
      }
      else if(tmp == "1") {
        status = "";
        continue;
      }
      else if(tmp == "2") {
        status = "";
        continue;
      }
      else if(tmp == "3") status = "";
      else if(tmp == "4") {
        status = "";
        money = "";
      }

      str =
        '{"address":"' + temp[i]         +
        '","name":"'   +
        (await Create.test1(temp[i], 4)) +
        '","vin":"'    +
        (await Create.test1(temp[i], 1)) +
        '","money":"'  + money           +
        '","state":"'  + status          +
        '","date":"'   +
        (await Create.test1(temp[i], 8)) +
        '"}';

      stick.push(str);
    }

    num = stick.length;
    for(var i = 0;i < num;i++) {
      result = result + stick[i];
      if(i < num-1) result = result + ",";
    }
    result = result + "]";
    this.tableData = JSON.parse(result);
  },
  methods: {
    valuation(row) {
      this.$prompt("()", "", {
        confirmButtonText: "",
        cancelButtonText: "",
      }).then(({ value }) => {
        Eva.evaluate(row.address,value);
        // this.$message({
        //   type: "success",
        //   message: ":" + value
        // });
      });
    },
    back(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        Eva.backwork(row.address);
      });
    },
    detail(row) {
      this.$router.push({name: 'detail',params:{ userid:row.address}});
    }
  },

  data() {
    return {
      activeIndex2: "1",
      tableData: [
        // {
        //   address: "0x6f167c41920d42f16B984524f9b131b5D6a0AB5f",
        //   name: "5 2019 540Li",
        //   vin: "W2BCRAFZ3S9M687JE",
        //   money: "100",
        //   state: "",
        //   date: "2018-12-20 11:28:48"
        // }
      ]
    };
  }
};
</script>

